body>.wp-site-blocks {
    height: 100%;
    display: flex;
    flex-direction: row;
    padding: 0;
    background: #f9f9f9;
    overflow: scroll;

    &>.content-wrap {
        flex: 1;
        flex-basis: 0;
    }
}

header.wp-block-template-part {
    height: 100%;
}

.wp-site-blocks>*+* {
    margin-block-start: 0;
}

.index-page {
    display: flex;
    width: 100%;
}

// search
.toolbox-box {
    display: none;

    .search-box {
        position: relative;
    }

    .search-icon {
        position: absolute;
        top: 16px;
        left: 26px;
        display: inline-block;
        width: 20px;
        height: 20px;
        background-image: url(https://self-affirm.com/wp-content/uploads/2023/06/sprite_img_header.png);
        background-position: 0 0;
        background-size: 93px 91px;
    }

    .search-input {
        display: block;
        padding: 0 60px;
        height: 50px;
        border-radius: 25px;
        background-color: #FFFFFF;
        border: 1px solid rgba(162, 162, 162, 0.1);
        box-shadow: 0px 0px 13px rgba(216, 216, 216, 0.25);

        &::placeholder {
            color: #474747;
        }
    }
}